import React,{ Component } from "react";
import './Footer.css'



class Footer extends Component {
    render(){
        return(
            <footer className="footer">
                <div className="container">
                    <span > Website &#169; {new Date().toLocaleDateString()} </span>
                </div>
            </footer>
        );
    }
}

export default Footer;


